'use client';

import { useState, useEffect } from 'react';
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';
import ProductCard from '@/components/ProductCard';
import { getAllProducts, getProductsByCategory, getAllCategories, Product, Category } from '@/lib/firebase/products';

export default function Products() {
  const [selectedCategory, setSelectedCategory] = useState<string>('all');
  const [products, setProducts] = useState<Product[]>([]);
  const [categories, setCategories] = useState<Category[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [categoriesLoading, setCategoriesLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  // Handle URL hash to filter by category when page loads or hash changes
  useEffect(() => {
    const handleHashChange = () => {
      const hash = window.location.hash.slice(1); // Remove the '#' symbol
      if (hash) {
        // Check if the hash matches any category ID
        const categoryExists = categories.some(cat => cat.id === hash);
        if (categoryExists) {
          setSelectedCategory(hash);
          // Scroll to top of product list
          setTimeout(() => {
            const productList = document.getElementById('product-list');
            if (productList) {
              productList.scrollIntoView({ behavior: 'smooth', block: 'start' });
            }
          }, 100);
        }
      }
    };

    // Check hash on initial load
    handleHashChange();

    // Listen for hash changes
    window.addEventListener('hashchange', handleHashChange);

    return () => {
      window.removeEventListener('hashchange', handleHashChange);
    };
  }, [categories]);

  // Fetch categories on mount from database
  useEffect(() => {
    const fetchCategories = async () => {
      setCategoriesLoading(true);
      try {
        const fetchedCategories = await getAllCategories();
        console.log('Categories fetched:', fetchedCategories.length, fetchedCategories);
        setCategories(fetchedCategories);
        
        if (fetchedCategories.length === 0) {
          console.warn('No categories found. Please add your collection names to lib/firebase/categories-config.ts');
        }
      } catch (err: any) {
        console.error('Error fetching categories from database:', {
          error: err,
          message: err?.message,
          code: err?.code
        });
        // Categories are fetched from database only, empty array will be returned on error
        setCategories([]);
      } finally {
        setCategoriesLoading(false);
      }
    };

    fetchCategories();
  }, []);

  // Fetch products when category changes
  useEffect(() => {
    const fetchProducts = async () => {
      setLoading(true);
      setError(null);
      try {
        // Get the selected category to find its collection name
        const category = categories.find(cat => cat.id === selectedCategory);
        const collectionName = category?.collectionName;
        
        const fetchedProducts = await getProductsByCategory(selectedCategory, collectionName);
        setProducts(fetchedProducts);
      } catch (err: any) {
        console.error('Error fetching products:', err);
        // Show specific error message if it's a permission error
        const errorMessage = err?.message?.includes('Access denied') 
          ? 'Access denied. Please check Firestore security rules configuration.'
          : 'Failed to load products. Please check your Firebase configuration.';
        setError(errorMessage);
      } finally {
        setLoading(false);
      }
    };

    fetchProducts();
  }, [selectedCategory, categories]);

  const selectedCategoryData = categories.find(cat => cat.id === selectedCategory);

  const handleCategoryChange = (categoryId: string) => {
    setSelectedCategory(categoryId);
    // Update URL hash without scrolling
    window.history.replaceState(null, '', `#${categoryId}`);
  };

  const handleKeyDown = (e: React.KeyboardEvent, categoryId: string) => {
    if (e.key === 'Enter' || e.key === ' ') {
      e.preventDefault();
      handleCategoryChange(categoryId);
    }
  };

  return (
    <div className="min-h-screen bg-[#f9fafb] text-gray-900">
      <Navbar />
      
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8 md:py-12">
        <div className="text-center mb-8 md:mb-12">
          <h1 className="text-4xl md:text-5xl font-bold text-gray-900 mb-4">
            Our Products
          </h1>
          <p className="text-lg md:text-xl text-gray-600 max-w-3xl mx-auto">
            Explore our comprehensive range of electronic components and equipment
          </p>
        </div>

        {/* Category Filter */}
        <section className="mb-8 md:mb-12" aria-label="Product categories">
          <div className="bg-white rounded-lg p-4 md:p-6 border border-gray-200 shadow-sm">
            <h2 className="text-lg font-semibold text-gray-900 mb-4 md:mb-6">
              Filter by Category
            </h2>
            {categoriesLoading ? (
              <div className="flex flex-wrap gap-3 md:gap-4">
                {[1, 2, 3, 4, 5].map((i) => (
                  <div
                    key={i}
                    className="h-12 w-32 bg-gray-200 rounded-lg animate-pulse"
                    aria-hidden="true"
                  />
                ))}
              </div>
            ) : categories.length > 0 ? (
              <div
                className="flex flex-wrap gap-3 md:gap-4"
                role="tablist"
                aria-label="Product category filters"
              >
                {/* All Products Button */}
                <button
                  onClick={() => handleCategoryChange('all')}
                  onKeyDown={(e) => handleKeyDown(e, 'all')}
                  role="tab"
                  aria-selected={selectedCategory === 'all'}
                  aria-controls="product-list"
                  tabIndex={selectedCategory === 'all' ? 0 : -1}
                  className={`
                    flex items-center gap-2 px-4 py-2.5 md:px-6 md:py-3 rounded-lg font-medium text-sm md:text-base
                    transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-[#3b82f6] focus:ring-offset-2
                    ${
                      selectedCategory === 'all'
                        ? 'bg-gradient-to-r from-gray-500 to-gray-600 text-white shadow-md scale-105'
                        : 'bg-gray-50 text-gray-700 hover:bg-gray-100 hover:shadow-sm border border-gray-200'
                    }
                  `}
                >
                  <span className="text-lg md:text-xl" aria-hidden="true">
                    ðŸ“¦
                  </span>
                  <span>All Products</span>
                </button>
                {/* Category Buttons - Loaded from Database */}
                {categories.map((category) => {
                  const isSelected = selectedCategory === category.id;
                  return (
                    <button
                      key={category.id}
                      onClick={() => handleCategoryChange(category.id)}
                      onKeyDown={(e) => handleKeyDown(e, category.id)}
                      role="tab"
                      aria-selected={isSelected}
                      aria-controls="product-list"
                      tabIndex={isSelected ? 0 : -1}
                      className={`
                        flex items-center gap-2 px-4 py-2.5 md:px-6 md:py-3 rounded-lg font-medium text-sm md:text-base
                        transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-[#3b82f6] focus:ring-offset-2
                        ${
                          isSelected
                            ? `bg-gradient-to-r ${category.gradient} text-white shadow-md scale-105`
                            : 'bg-gray-50 text-gray-700 hover:bg-gray-100 hover:shadow-sm border border-gray-200'
                        }
                      `}
                    >
                      <span className="text-lg md:text-xl" aria-hidden="true">
                        {category.icon}
                      </span>
                      <span>{category.name}</span>
                    </button>
                  );
                })}
              </div>
            ) : (
              <div className="text-center py-8">
                <p className="text-gray-600 font-medium">No categories configured.</p>
                <div className="mt-4 text-left max-w-2xl mx-auto bg-gray-50 p-4 rounded-lg">
                  <p className="text-gray-700 text-sm mb-2 font-semibold">Setup Instructions:</p>
                  <ol className="text-gray-600 text-sm space-y-2 list-decimal list-inside">
                    <li>Open <code className="bg-gray-200 px-1 rounded">lib/firebase/categories-config.ts</code></li>
                    <li>Add your Firestore collection names to the <code className="bg-gray-200 px-1 rounded">categoryConfig</code> array</li>
                    <li>For each collection (like "Electronics", "Sweets"), add an entry with:
                      <ul className="ml-6 mt-1 space-y-1 list-disc">
                        <li><code className="bg-gray-200 px-1 rounded">collectionName</code>: your Firestore collection name (must match exactly)</li>
                        <li><code className="bg-gray-200 px-1 rounded">id</code>, <code className="bg-gray-200 px-1 rounded">name</code>, <code className="bg-gray-200 px-1 rounded">icon</code>, <code className="bg-gray-200 px-1 rounded">gradient</code></li>
                      </ul>
                    </li>
                    <li>Save the file and refresh this page</li>
                  </ol>
                  <p className="text-gray-600 text-xs mt-3 italic">Check browser console (F12) for detailed error messages</p>
                </div>
              </div>
            )}
          </div>
        </section>

        {/* Products Display */}
        <section
          id="product-list"
          role="tabpanel"
          aria-labelledby={`category-${selectedCategory}`}
        >
          {selectedCategoryData && selectedCategory !== 'all' && (
            <div className="mb-6 md:mb-8">
              <div className="flex items-center gap-3 mb-2">
                <div className={`w-12 h-12 rounded-lg bg-gradient-to-br ${selectedCategoryData.gradient} flex items-center justify-center text-2xl shadow-md`}>
                  {selectedCategoryData.icon}
                </div>
                <div>
                  <h2 className="text-2xl md:text-3xl font-bold text-gray-900">
                    {selectedCategoryData.name}
                  </h2>
                  <p className="text-gray-600 text-sm md:text-base">
                    {loading ? 'Loading...' : `${products.length} ${products.length === 1 ? 'product' : 'products'} found`}
                  </p>
                </div>
              </div>
            </div>
          )}

          {loading ? (
            <div className="bg-white rounded-lg p-8 md:p-12 border border-gray-200 shadow-sm text-center">
              <p className="text-gray-600 text-lg md:text-xl">Loading products...</p>
            </div>
          ) : error ? (
            <div className="bg-white rounded-lg p-8 md:p-12 border border-gray-200 shadow-sm text-center">
              <p className="text-red-600 text-lg md:text-xl">{error}</p>
              <p className="text-gray-600 mt-2">Please check your Firebase configuration.</p>
            </div>
          ) : products.length > 0 ? (
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 md:gap-6">
              {products.map((product) => (
                <ProductCard
                  key={product.id}
                  id={product.id}
                  name={product.name}
                  description={product.description}
                  imageUrl={product.imageUrl}
                />
              ))}
            </div>
          ) : (
            <div className="bg-white rounded-lg p-8 md:p-12 border border-gray-200 shadow-sm text-center">
              <p className="text-gray-600 text-lg md:text-xl">
                No products found in this category.
              </p>
            </div>
          )}
        </section>

        {/* Call to Action */}
        <section className="mt-16 md:mt-24 text-center">
          <div className="bg-gradient-to-r from-[#3b82f6] to-[#14b8a6] rounded-lg p-8 md:p-12 shadow-lg">
            <h2 className="text-3xl md:text-4xl font-bold text-white mb-4">
              Need Custom Solutions?
            </h2>
            <p className="text-xl text-white/90 mb-8 max-w-2xl mx-auto leading-relaxed">
              Contact us for bulk orders, custom specifications, or technical consultation
            </p>
            <a
              href="/contact"
              className="inline-block bg-white text-gray-900 px-8 py-3 rounded-lg font-semibold hover:bg-gray-50 transition-all shadow-md hover:shadow-lg focus:outline-none focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-transparent"
            >
              Get in Touch
            </a>
          </div>
        </section>
      </main>

      <Footer />
    </div>
  );
}
