import { 
  collection, 
  getDocs, 
  getDoc,
  doc,
  query, 
  where, 
  orderBy,
  DocumentData,
  QuerySnapshot
} from 'firebase/firestore';
import { db } from './config';
import { collectionNamesToCheck, createCategoryFromCollectionName } from './categories-config';

export interface Product {
  id: string;
  name: string;
  description: string;
  categoryId: string;
  imageUrl?: string;
  price?: number;
  inStock?: boolean;
  createdAt?: any;
}

export interface Category {
  id: string;
  name: string;
  icon: string;
  gradient: string;
  description?: string; // Optional description for category
  order?: number; // Optional order field for sorting
  collectionName?: string; // Name of the Firestore collection for this category (e.g., "Electronics")
}

/**
 * Check if error is a permission denied error
 */
function isPermissionError(error: any): boolean {
  return error?.code === 'permission-denied' || 
         error?.code === 7 || 
         error?.message?.toLowerCase().includes('permission') ||
         error?.message?.toLowerCase().includes('missing or insufficient');
}

/**
 * Fetch all products from Firestore
 * Works with Firestore security rules - tries orderBy first, falls back to simple query if orderBy is not allowed
 */
export async function getAllProducts(): Promise<Product[]> {
  try {
    const productsRef = collection(db, 'products');
    
    // Try with orderBy first (if rules allow it)
    try {
      const q = query(productsRef, orderBy('createdAt', 'desc'));
      const querySnapshot: QuerySnapshot<DocumentData> = await getDocs(q);
      
      const products: Product[] = [];
      querySnapshot.forEach((doc) => {
        products.push({
          id: doc.id,
          ...doc.data(),
        } as Product);
      });
      
      return products;
    } catch (orderByError: any) {
      // If orderBy fails (e.g., due to missing index or rules), try without it
      if (orderByError?.code === 'failed-precondition') {
        // Index missing - try without orderBy
        try {
          const querySnapshot: QuerySnapshot<DocumentData> = await getDocs(productsRef);
          
          const products: Product[] = [];
          querySnapshot.forEach((doc) => {
            products.push({
              id: doc.id,
              ...doc.data(),
            } as Product);
          });
          
          // Sort client-side as fallback
          products.sort((a, b) => {
            const aDate = a.createdAt?.toMillis?.() || 0;
            const bDate = b.createdAt?.toMillis?.() || 0;
            return bDate - aDate;
          });
          
          return products;
        } catch (fallbackError: any) {
          // If fallback also fails, check if it's a permission error
          if (isPermissionError(fallbackError)) {
            // Permission denied - return empty array gracefully
            return [];
          }
          throw fallbackError;
        }
      } else if (isPermissionError(orderByError)) {
        // Permission error on orderBy - try simple query
        try {
          const querySnapshot: QuerySnapshot<DocumentData> = await getDocs(productsRef);
          
          const products: Product[] = [];
          querySnapshot.forEach((doc) => {
            products.push({
              id: doc.id,
              ...doc.data(),
            } as Product);
          });
          
          // Sort client-side as fallback
          products.sort((a, b) => {
            const aDate = a.createdAt?.toMillis?.() || 0;
            const bDate = b.createdAt?.toMillis?.() || 0;
            return bDate - aDate;
          });
          
          return products;
        } catch (fallbackError: any) {
          // If fallback also fails with permission error, return empty array
          if (isPermissionError(fallbackError)) {
            // Permission denied - return empty array gracefully
            return [];
          }
          throw fallbackError;
        }
      }
      throw orderByError;
    }
  } catch (error: any) {
    if (isPermissionError(error)) {
      // Permission denied - return empty array gracefully
      return [];
    }
    console.error('Error fetching products:', error);
    return [];
  }
}

/**
 * Fetch products by category
 * Works with Firestore security rules - handles permission errors and query restrictions
 * If category has collectionName, fetches from that collection. Otherwise uses categoryId.
 */
export async function getProductsByCategory(categoryId: string, categoryCollectionName?: string): Promise<Product[]> {
  try {
    if (categoryId === 'all') {
      return await getAllProducts();
    }
    
    // Use collectionName if provided, otherwise default to 'products' collection with categoryId filter
    const collectionName = categoryCollectionName || 'products';
    const productsRef = collection(db, collectionName);
    
    console.log(`Fetching products from collection: ${collectionName} for category: ${categoryId}`);
    
    // Try with orderBy first (if rules allow it and we're using a dedicated collection)
    try {
      const q = categoryCollectionName 
        ? query(productsRef, orderBy('createdAt', 'desc'))
        : query(
            productsRef, 
            where('categoryId', '==', categoryId),
            orderBy('createdAt', 'desc')
          );
      const querySnapshot: QuerySnapshot<DocumentData> = await getDocs(q);
      
      const products: Product[] = [];
      querySnapshot.forEach((doc) => {
        products.push({
          id: doc.id,
          categoryId: categoryId, // Ensure categoryId is set
          ...doc.data(),
        } as Product);
      });
      
      console.log(`Fetched ${products.length} products from ${collectionName}`);
      return products;
    } catch (orderByError: any) {
      // If orderBy fails, try without it
      if (orderByError?.code === 'failed-precondition' || isPermissionError(orderByError)) {
        try {
          const q = categoryCollectionName
            ? query(productsRef)
            : query(productsRef, where('categoryId', '==', categoryId));
          const querySnapshot: QuerySnapshot<DocumentData> = await getDocs(q);
          
          const products: Product[] = [];
          querySnapshot.forEach((doc) => {
            products.push({
              id: doc.id,
              categoryId: categoryId,
              ...doc.data(),
            } as Product);
          });
          
          // Sort client-side as fallback
          products.sort((a, b) => {
            const aDate = a.createdAt?.toMillis?.() || 0;
            const bDate = b.createdAt?.toMillis?.() || 0;
            return bDate - aDate;
          });
          
          console.log(`Fetched ${products.length} products from ${collectionName} (without ordering)`);
          return products;
        } catch (fallbackError: any) {
          console.error(`Error fetching from ${collectionName}:`, fallbackError);
          if (isPermissionError(fallbackError)) {
            return [];
          }
          throw fallbackError;
        }
      }
      throw orderByError;
    }
  } catch (error: any) {
    if (isPermissionError(error)) {
      return [];
    }
    console.error('Error fetching products by category:', error);
    return [];
  }
}

/**
 * Fetch a single product by ID
 * Works with Firestore security rules - handles permission errors
 */
export async function getProductById(productId: string): Promise<Product | null> {
  try {
    const productRef = doc(db, 'products', productId);
    const productSnap = await getDoc(productRef);
    
    if (!productSnap.exists()) {
      return null;
    }
    
    return {
      id: productSnap.id,
      ...productSnap.data(),
    } as Product;
  } catch (error: any) {
    if (isPermissionError(error)) {
      // Permission denied - return null gracefully
      return null;
    }
    console.error('Error fetching product by ID:', error);
    return null;
  }
}

/**
 * Fetch all categories from configuration
 * Categories are automatically generated from your collection names defined in categories-config.ts
 */
export async function getAllCategories(): Promise<Category[]> {
  try {
    const categories = getCategoriesFromConfig();
    
    console.log(`Loaded ${categories.length} categories from configuration:`, categories.map(c => c.name));
    
    // Verify collections exist by attempting to access each
    // This helps catch errors early, but we'll still show categories even if verification fails
    const verifiedCategories: Category[] = [];
    
    for (const category of categories) {
      if (category.collectionName) {
        try {
          // Try to access the collection (just check if we can read it)
          const testRef = collection(db, category.collectionName);
          const testQuery = query(testRef);
          await getDocs(testQuery);
          
          verifiedCategories.push(category);
          console.log(`âœ“ Verified collection: ${category.collectionName}`);
        } catch (error: any) {
          if (isPermissionError(error)) {
            console.warn(`âš  Permission denied for collection: ${category.collectionName}. Category will still be shown.`);
          } else {
            console.warn(`âš  Could not verify collection: ${category.collectionName}. Error:`, error.message);
          }
          // Still add it - might work when actually fetching products
          verifiedCategories.push(category);
        }
      } else {
        // Category without collectionName - skip it
        console.warn(`âš  Category "${category.name}" missing collectionName field`);
      }
    }
    
    return verifiedCategories;
  } catch (error: any) {
    console.error('Error loading categories from configuration:', error);
    return [];
  }
}

